html {
    height: 100%;
}
/*Nav*/
.nav-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: lightblue;
}
.nav-logo{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.nav-img{
    width: 100px;
    height: 130px;
    position: absolute;
}
.button-group{
    display: flex;
    flex-direction: row;
    align-items: center;
}
#category-btn-back {
    margin-right: 15px;
    color: black;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
#category-btn{
    border-radius: 20px;
    margin-right: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.material-icons{
    margin-right: 5px;
}
/*Categories content*/
.main-content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    background-image: url("../Style/images/backgr.png");
    background-size: cover;
    background-repeat: no-repeat;
}

.category-items {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 280px;

}

.category-title {
    display: flex;
    justify-content: center;
    font-family: fantasy;
    font-weigh: bold;
}

.content-btn {
    width: 150px;
    height: 40px;
    border-radius: 30px;

}
.content-btn::after{
    background-color: grey;
}
.wrapper {
    margin: 5px;
    text-align: center;
}
.content-logo{
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
}
.content-img{
    width: 200px;
    height: 200px;
    position: absolute;
}
/*Location content*/
.location-details {
    display: flex;
    align-items: center;
    flex-direction: column;

}

.location-title {
    font-family: fantasy;
    font-weigh: bold;
}

.location-table, .form-content{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 90%;
}

/*Table content*/
div.MuiInput-underline:before {
    border-bottom: none;
    content: none;
}

div.MuiInput-underline:after {
    border-bottom: none;
    content: none;
}

/*Form content*/
.form-profile {
    margin-top: 20px;
}

.form-btn {
    display: flex;
    justify-content: center;
}